// HEADER :: START

.header {
	position: relative;
	display: flex;
	flex-direction: column;

	@import "header--conatainer";
	@import "header--wrapper";
	@import "header--toggle";
	@import "header--action";
	@import "header--category";
	@import "header--view";
	@import "header--nav";
	@import "header--recent";
	@import "header--update";

	@include adaptive(767) {
		position: fixed;
		width: 100%;
		z-index: 100;
	}
}

// HEADER :: END
